@import url('https://fonts.googleapis.com/css?family=Oswald:100');

.Contact{
  width:100%;
  height:98%;
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  background-color: white;;
  z-index: 5;
}


.profile{
  width:100px;
  margin:auto;
  margin-left: 0;
  max-width: 200px;
}

.con-body{
  bottom: 0;
  width:100%;
  margin:auto;
  margin-top:0;
  /* position: absolute; */
}

.con-body h4{
  margin:auto;
  text-align: center;
}

.a{
  margin:auto;
  width:150px;
  height:auto;
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  text-align: center;
  text-decoration: none;
  color:inherit;
  margin-top:30px;
}

.a h3{
  margin-top: 0;
}

.a:hover, a:focus, a:visited{
  text-decoration: none;
  color: inherit;
}

.con-body a div{
  margin:auto;
  margin-top:0;
  width:150px;
  height:150px;
  background-image: url(https://assets-cdn.github.com/images/modules/logos_page/Octocat.png);
  background-size: contain;
  background-repeat: no-repeat;
}

.form{
  margin:auto;
  padding: 5% 20% 10% 20%;
  width:100%;
  /* display: flex; */
  /* flex-direction: column; */
  background-color: #242424;
  text-align: center;
}

.form-title {
  margin-bottom: 3%;
  font-family: 'Oswald', sans-serif;
  font-weight: 100;
  font-size: 36px; 
  color: white;
}

.area input, .area textarea {
  /* display: flex; */
  width: 100%;
  margin: 20px auto;
  /* justify-content: flex-start; */
  outline: none;
  border: none;
  border-radius: 5px;
  padding:10px;
  color: #242424;
  font-size: 16px;
  background-color: #DFE7FB;
  clear: both;
}

.area textarea{
  height: 150px;
  margin-top: 20px;;
  width: 100%;
  /* resize: none; */
  height: 150px;
  clear: both;
}

.save{
  margin: 50px auto;
  padding: 2px 10px;
  width: 165px;
  height: 50px;
  border: 1px solid white;;
  font-size: 16px;
  background-color: transparent;
  border-radius: 5px;
  font-family: 'Oswald', sans-serif;
  font-weight: 100;
  font-size: 20px; 
  color: white; 
  outline: none;
}

/* .save i{color:#FFF} */
.save:hover, .save:focus{
  background-color: rgba(159, 178, 202, 0.8);
}

/* ---------------------------------------MOBILE--------------------------------------- */
@media (min-width: 320px) and (max-width: 734px) {
  /* temp  */
  #about {
    height: 100%;
    /* margin-top: 400px; */
    position: relative;
  }

  /* end temp */
  .form {
    padding: 10% 10%;
  }
}



